import React from 'react';
import { arrayOf, string, shape } from 'react-proptypes';

import { Button } from '../../../ui';
import { Skill } from '../../../components/Skill';
import './Features.scss';
import Link from 'gatsby-link';

const Features = ({ params }) => (
    <div className="features__container">
        <div className="features__background"
            style={{ backgroundImage: `url(${process.env.ASSETS_URL}${params.bgImage})` }} />

        <div className="features__content">
            <div className="features__items">
                {
                    params.items.map((item, index) => Skill(item, index))
                }
            </div>
        </div>

        <div className="features__link">
            <Link
                to={'/skills'}>
                <Button
                    primary
                    outline >
                    Learn more
                </Button>
            </Link>
        </div>
    </div>
);

Features.propTypes = {
    items: arrayOf(shape({
        title: string,
        icon: string,
        color: string,
        text: string
    }))
};

Features.defaultProps = {
    items: [],
    bgImage: string,
    title: string
};

export default Features;



